import { useCartContext } from "../CartContext"
import { Product } from "../shared/types"

export interface ProductCardProps {
  datum: Product
}

export const ProductCard = ({
  datum,
}: ProductCardProps) => {
  const { addToCart, products } = useCartContext()

  const isInCart = !!products?.find(
    (product) => datum.name === product.name
  )

  return (
    <div className="nes-container is-rounded item">
      <img
        style={{ imageRendering: "pixelated" }}
        src={`/mario-shop${datum.image}`}
        width="64px"
        height="64px"
        alt="goblin"
      />
      <p>{datum.name}</p>
      <p>{datum.price} Mb</p>
      {isInCart ? (
        <button disabled className="nes-btn is-disabled">
          已添加
        </button>
      ) : (
        <button
          className="nes-btn is-primary"
          onClick={() => {
            addToCart(datum)
          }}
        >
          添加
        </button>
      )}
    </div>
  )
}
